// });

const checkStatus = response => {
  if (response.status >= 200 && response.status < 300) {
    return response
  }
  const errortext = codeMessage[response.status] || response.statusText
  notification.error({
    message: errortext,
    duration:2,
  })
  const error = new Error(errortext)
  error.name = response.status
  error.response = response
  throw error
}

const checkCode = response => {
  const { data } = response
  const { code, message } = data
  if (code < 0) {
    return { data:'', message }
  }
  return data
}

const requestMethods = {
  'POST':(url, params, headers) => instance.post(url, params, { headers }),
  'GET':(url, params, headers) => instance.get(url, {
    params: {
      ...params,
    },
  }, { ...headers }),
}

export default function request(url, option) {
  // let _url = ''
  const options = { ...option }
  const token = GetLocalStorage('token')
  // const headers = { ...option.headers }
  // const newOptions = {  ...options, headers }
  const headers = { ...(token ? { token } : {}) }

  const newOptions = { ...options, headers }
  if (
    newOptions.method === 'POST'
    || newOptions.method === 'PUT'
    || newOptions.method === 'DELETE'
  ) {
    // _url = url
    if (!(newOptions.body instanceof FormData)) {
      newOptions.headers = {
        Accept: 'application/json',
        'Content-Type': 'application/json; charset=utf-8',
        ...newOptions.headers,
      }
    } else {
      newOptions.headers = {
        Accept: 'application/json',
        ...newOptions.headers,
      }
    }
  }
  const { body, method } = newOptions
  return requestMethods[method](url, body, newOptions.headers)
    .then(checkStatus)
    .then(checkCode)
    .then(response => {
      console.log(response, '-----response------')
      return response
    })
    .catch(e => {
      console.log(e)
    })
}